<template>
    <div class="content-box">
        <div class="title-box">
            <p class="header-line">
                <i class="fa fa-arrow-left m-r-10 pointer" aria-hidden="true" @click="back()"></i>
                {{$t('dockerControl.containerList')}}
            </p>
        </div>
        <div class="select-box">
            <div v-if="productname">
                <div class="container-box m-l-10">
                    <span class="label">{{$t('dockerControl.projectName')}}:</span>
                    <span class="cvalue">{{productname}}</span>
                </div>
                <div class="container-box m-l-10">
                    <span class="label">{{$t('dockerControl.version')}}:</span>
                    <span class="cvalue">{{version}}</span>
                </div>
            </div>
            <div v-else>
                <mode-switch @mode-status="changeMode" ></mode-switch>
                <span class="vertical"></span>
                <select-group 
                @select-getDeviceOptions="getGroupOption" 
                @select-getGroupName="getGroupName" 
                :isSingleMode="isSingleMode"
                :groupType="groupTypeArray">
                </select-group>
                <select-device 
                @select-device="getDeviceOption"
                @isOfflineDev="isOfflineDev" 
                :isSingleMode="isSingleMode"
                :isDisabledOffDev="isDisabledOffDev"
                :groupTypeArray="groupTypeArray">
                </select-device> 
            </div>
            
        </div>
        <div class="btn-group" v-if="btnGroupIsShow" id="actionID" :class="{fixedTop: isFixed}">
            <el-button-group>
                <el-button type="success" size="mini" :disabled="startbtndisabled" @click="composeAction('startContainer')">
                    <i class="fa fa-play m-r-5"></i>{{$t('global.start')}}</el-button>
                <el-button type="danger" size="mini" :disabled="stopbtndisabled" @click="composeAction('stopContainer')">
                    <i class="fa fa-stop m-r-5"></i>{{$t('global.stop')}}</el-button>
                <el-button type="danger" size="mini" :disabled="killbtndisabled" @click="composeAction('killContainer')">
                    <i class="fa fa-bomb m-r-5"></i>{{$t('global.kill')}}</el-button>
                <el-button type="primary" size="mini" :disabled="restartbtndisabled" @click="composeAction('restartContainer')">
                    <i class="fa fa-refresh m-r-5"></i>{{$t('global.restart')}}</el-button>
                <el-button type="danger" size="mini" :disabled="pausebtndisabled" @click="composeAction('pauseContainer')">
                    <i class="fa fa-pause m-r-5"></i>{{$t('global.pause')}}</el-button>
                <el-button type="primary" size="mini" :disabled="resumebtndisabled" @click="composeAction('resumeContainer')">
                    <i class="fa fa-play m-r-5"></i>{{$t('global.resume')}}</el-button>
                <el-button type="danger" size="mini" :disabled="removebtndisabled" @click="composeAction('removeContainer')">
                    <i class="fa fa-trash-o m-r-5"></i>{{$t('global.remove')}}</el-button>
            </el-button-group>
        </div>
        <div class="table-box pkgStatusContent">
            <div class="search" v-show="isShowSearchInput"> 
                <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('dockerControl.containerKeywordHolder')" >  
                </el-input>
                <i class="fa fa-search search-icon" aria-hidden="true"></i>
            </div>
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane name="container">
                    <span slot="label">
                        <i class="fa fa-sellsy m-r-5" aria-hidden="listLoading"></i>
                        {{$t('dockerControl.container')}}
                    </span>
                    <el-table
                    :data="containerTableList"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading"
                    @selection-change="selectItem">
                    <el-table-column
                    type="selection"
                    min-width="50">
                    </el-table-column>
                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img src="@/assets/imgs/container.png" alt="no img" style="width:48px"/>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="name"
                    :label="$t('dockerControl.name')"
                    min-width="90"
                    sortable
                    >
                    </el-table-column>
                    <el-table-column
                    prop="compose"
                    :label="$t('dockerControl.stack')"
                    min-width="120"
                    sortable>
                        <template slot-scope="scope">
                            {{scope.row.compose|na}}
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="state"
                    :label="$t('dockerControl.state')"
                    min-width="80"
                    :filters="[
                    { text: $t('dockerControl.running'), value: 'running' }, 
                    { text: $t('dockerControl.paused'), value: 'paused' }, 
                    { text: $t('dockerControl.exited'), value: 'exited' },
                    { text: $t('dockerControl.created'), value: 'created' }]"
                    :filter-method="filterStatus"
                    filter-placement="bottom-end">
                    <template slot-scope="scope">
                        <div>
                            <span class="badge bg-green" v-if="scope.row.state=='running'">{{$t('dockerControl.running')}}</span>
                            <span class="badge bg-yellow" v-else-if="scope.row.state=='paused'">{{$t('dockerControl.paused')}}</span>
                            <span class="badge bg-red" v-else-if="scope.row.state=='exited'">{{$t('dockerControl.exited')}}</span>
                            <span class="badge bg-SteelBlue" v-else-if="scope.row.state=='created'">{{$t('dockerControl.created')}}</span>
                            <span class="badge bg-DimGray" v-else>{{scope.row.state}}</span>
                        </div>
                    </template>
                    </el-table-column>
                    <el-table-column
                    :label="$t('dockerControl.quickAction')"
                    min-width="120">
                        <template slot-scope="scope">
                            <div>
                                <el-tooltip content="Log" placement="top">
                                <i class="fa fa-file-text m-r-5 c-primary pointer" aria-hidden="true"
                                @click="getContent('getContainerLog', scope.row)"></i>
                                </el-tooltip>
                                <el-tooltip content="Inspect" placement="top">
                                <i class="fa fa-info-circle m-r-5 c-primary pointer" aria-hidden="true"
                                @click="getContent('getContainerInspect', scope.row)"></i>
                                </el-tooltip>
                                <el-tooltip content="Monitor" placement="top">
                                <i class="fa fa-area-chart m-r-5 c-primary pointer" aria-hidden="true"
                                @click="monitor(scope.row.name, scope.row.id)" v-if="scope.row.state=='running'||scope.row.state=='paused'"></i>
                                </el-tooltip>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    :label="$t('dockerControl.cmd')"
                    min-width="120">
                        <template slot-scope="scope">
                            {{scope.row.command|none}}
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="image"
                    :label="$t('dockerControl.image')"
                    min-width="180">
                    </el-table-column>
                    <el-table-column
                    prop="created"
                    :label="$t('dockerControl.created')"
                    min-width="120"
                    sortable>
                        <template slot-scope="scope">
                            {{scope.row.created|time}}
                        </template>
                    </el-table-column>
                    <el-table-column
                    :label="$t('dockerControl.publishedPort')"
                    min-width="120">
                        <template slot-scope="scope">
                            {{scope.row.ports|none}}
                        </template>
                    </el-table-column>
                </el-table>
                </el-tab-pane>
                <el-tab-pane name="containerStatusHistory">
                    <span slot="label">
                        <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                        {{$t('dockerControl.history')}}
                    </span>
                    <div v-if="isSingleMode">
                        <container-history
                        :statusType="singleContainerType"
                        ref="containerHistory"
                        @isFinished="isFinished"
                        :selectedAgentId="selectedAgentId"
                        >
                        </container-history>
                    </div>
                    <div v-else>
                         <container-history
                        :statusType="batchContainerType"
                        ref="containerHistory"
                        @isFinished="isFinished"
                        :groupname="groupname"
                        @getStatusCount="getBatchComposeStatusCount"
                        >
                        </container-history>
                    </div>
                </el-tab-pane>
            </el-tabs>
            <!-- batch -->
            <div class="appStatusCount" v-show="isShowOverview">
                <span>{{$t('label.taskName')}} {{container_taskname}}</span>
                <span>{{$t('dockerControl.total')}} {{total}}</span>
                <span>{{$t('dockerControl.deploying')}} {{installing}}</span>
                <span>{{$t('dockerControl.success')}} {{success}}</span>
                <span>{{$t('dockerControl.failed')}} {{failed}}</span>
            </div>
        </div>
        <el-dialog  :visible.sync="dialogVisible" >
            <div slot="title" class="dialog-title">
                <i class="fa fa-file-text m-r-10" aria-hidden="true"></i>
                Log
            </div>
            <div class="text-center" v-loading="logLoading">
                <editor v-model="content" @init="editorInit" lang="html" theme="chrome"  height="550"
                :options="{
                enableBasicAutocompletion: true,
                enableSnippets: true,
                enableLiveAutocompletion: true,
                tabSize:2,
                fontSize:14,
                showPrintMargin:false,   //去除编辑器里的竖线
                }"></editor>
            </div>
            
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false" size="small">{{$t('global.cancel')}}</el-button>
            </div>
        </el-dialog>
        <el-dialog  :visible.sync="inspectDialogVisible">
            <div slot="title" class="dialog-title">
                <i class="fa fa-info-circle m-r-10" aria-hidden="true"></i>
                Inspect
            </div>
            <div  v-loading="logLoading">
                <!-- <editor v-model="content" @init="editorInit" lang="html" theme="chrome"  height="550"></editor> -->
                <tree-view :data="inspectContent" :options="{maxDepth: 5}"></tree-view>
            </div>
            
            <div slot="footer" class="dialog-footer">
                <el-button @click="inspectDialogVisible = false" size="small">{{$t('global.cancel')}}</el-button>
            </div>
        </el-dialog>

        <el-dialog :visible.sync="dialogTasknameVisible" @close="initContainerInfo()" :close-on-click-modal="false">
            <div slot="title" class="dialog-title">
                <i class="fa fa-level-up m-r-10" ></i>
                {{$t('dockerControl.batchInstall')}}
            </div>
            <el-form :model="dockerStatusForm" :rules="rules" ref="dockerStatusForm">
                <el-form-item :label="$t('label.taskName')" label-width="230px" prop="taskname">
                    <el-input v-model.trim="dockerStatusForm.taskname" autocomplete="off" class="max-w-350" :placeholder="$t('dockerControl.taskName')"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogTasknameVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="batchinstall()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>

        
    </div>
</template>
<style lang="scss" scoped>
@import "../../assets/css/colors"; 
    .icon{
        color: #337ab7;
    }
    .btn-group{
        padding: 10px;
        background: #fff;
        margin: 10px 0;
        border-radius: 5px;
    }
    .container-box{
        display: inline-block;
        .label{
            font-size: 15px;
        }
        .cvalue{
            display: inline-block;
            min-width: 35px;
            text-align: center;
            border-bottom: 1px solid #ffffff;
            margin-left: 5px;
            font-style:italic;
            padding: 5px;
        }
    }
    .pkgStatusContent{
        position: relative;
        .appStatusCount{
            position: absolute;
            top: 30px;
            right: 6px;
            span{
                font-weight: 500;
                font-size: 11px;
                &:nth-child(1){
                    color: $primary-color;
                }
                &:nth-child(2){
                    border-left: 2px solid #ddd;
                    padding-left: 3px; 
                    color: $info-color;
                }
                &:nth-child(3){
                    color: $warn-color;
                }
                &:nth-child(4){
                    color: $success-color;
                }
                &:nth-child(5){
                    color: $darkgray-color;
                }
            }
            
        }
    }

    
    .search{
        position: absolute;
        top: 20px;
        right: 20px;
        width: 230px;
        z-index: 1001;
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
</style>
<script>
    import selectDevice from '../../common/select-device'
    import modeSwitch from "../../common/mode-switch"
    import selectGroup from "../../common/select-group"
    import singleControl from "./dockercontroldetails/single"
    import batchControl from "./dockercontroldetails/batch"
    import {groupType, singleContainerType, batchContainerType} from "../../assets/js/constant"
    import {dockerAction} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus, batchExecDeviceStatus } from '@/components/restfulapi/deviceStatusApi';
    import containerHistory from "./containercontroldetails/history"
    import{getAppStatusOverviewApi} from "@/components/restfulapi/appStatusApi"
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../assets/js/storage"
    export default{
        name: 'comtainercontrol',
        data(){
            let validateTaskname = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                selectedAgentId: '',
                containerTableData: [],
                selectedAgentsData: [],
                isSingleMode: true,
                groupname: '',
                //offline device is disabled
                isDisabledOffDev: true,
                //selectedAgent is offline
                isOffline: false,
                groupType: groupType.linux,
                groupTypeArray: [groupType.linux, groupType.windows],
                listLoading: false,
                productname: "",
                multipleSelection: [],
                dialogVisible: false,
                inspectDialogVisible: false,
                content: "",
                inspectContent: "",
                version: "",
                singleContainerType,
                
                activeName: "container",

                //batch
                 dockerStatusForm:{
                    taskname: '',
                },
                rules:{
                    taskname: {required: true, validator: validateTaskname, trigger: 'blur'}
                },
                batchContainerType,
                container_taskname: '',
                success: 0,
                installing: 0,
                failed: 0,
                keywords: '',
                dialogTasknameVisible: false,
                cid: "",
                logLoading: false,
                isFixed: false,
                offsetTop: 10000
            }
        },
        components:{
            selectDevice,
            selectGroup,
            modeSwitch,
            singleControl,
            batchControl,
            containerHistory,
            editor: require('vue2-ace-editor'),
        },
        methods:{
            getSensorStatus(){
                let data = {};
                if(this.isSingleMode){
                    if(!this.selectedAgentId) {
                        console.error("agentId is empty");
                        return;
                    }
                    if(this.productname){
                        data.productname = this.productname;
                        data.version = this.version;
                    }
                    this.listLoading = true;
                    getDeviceStatus(this.selectedAgentId, dockerAction["getContainers"], data).then((obj) => {
                        this.listLoading = false;
                        handleResponse(obj, (res) => {
                            if(res.status == "CONTENT"){
                                this.containerTableData = JSON.parse(res.content.value);
                            }
                        })   
                    })
                }else{
                    if(this.selectedAgentsData.length == 0){
                        console.error("getSensorStatus: agentData is null");
                        return;
                    }
                    this.listLoading = true;
                   getDeviceStatus(this.selectedAgentsData[0].value, dockerAction["getContainers"]).then((obj) => {
                        this.listLoading = false;
                        handleResponse(obj, (res) => {
                            if(res.status == "CONTENT"){
                                this.containerTableData = JSON.parse(res.content.value);
                            }
                        })   
                    }) 
                }
                
            },

            
            getDeviceOption(selectedAgentId){ 
                this.initData();
                this.selectedAgentId = selectedAgentId;
                this.getSensorStatus();
            },

            getGroupOption(selectedAgentsData){
                this.selectedAgentsData = selectedAgentsData;
            },

            initData(){
                this.containerTableData = [];
            },

            initContainerInfo(){
                this.dockerStatusForm.taskname = "";
                this.cid = "";
            },

            changeMode(val){
                this.initData();
                this.isSingleMode = !val
            },

            getGroupName(groupname){
                this.initData();
                this.groupname = groupname;
                this.getSensorStatus();
            },

            isDisabled(value){
                this.isDisabledOffDev = value;
            },
            isOfflineDev(value){
                this.isOffline = value
            },
            handleClick(tab){
                if(tab.name === "containerStatusHistory"){
                    if(!this.isSingleMode){
                        this.getBatchComposeStatusCount();
                    }
                    if(this.$route.query.name){
                       this.$refs.containerHistory.intervalGetContainerStatusHistory();
                    }
                    this.isDisabledOffDev = false;
                }else{
                    this.isDisabledOffDev = true
                }
            },
            isFinished(){
                this.getSensorStatus();
            },

            actionComposeSensor(cid, appdata){
                if(dockerAction[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }

                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("dockerAction."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        _g.openGlobalLoading();
                        execDeviceStatus(this.selectedAgentId, dockerAction[cid], appdata).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status == "CHANGED"){
                                    this.activeName = "containerStatusHistory";
                                    this.$refs.containerHistory.intervalGetContainerStatusHistory();
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }
                })
            },
            selectItem(val) {
                this.multipleSelection = val;
            },
            composeAction(cid){
                if(this.isSingleMode){
                    let containerInfo = {};
                    let data = [];
                    let taskname = this.singleContainerType+ (new Date()).getTime();
                    this.multipleSelection.forEach(element => {
                        let obj = {};
                        obj.name = element.name;
                        obj.containerId = element.id
                        data.push(obj);
                    });

                    containerInfo.taskname = taskname;
                    containerInfo.content = JSON.stringify(data)
                    
                    this.actionComposeSensor(cid, containerInfo)
                }else{
                    this.cid = cid;
                    this.dockerStatusForm.taskname = "";
                    this.dialogTasknameVisible = true;
                }
                
            },

            back() {
                this.$router.go(-1);
            },

            filterStatus(value, row){
                return row.state === value ;
            },

            editorInit: function () {
                require('brace/ext/language_tools') //language extension prerequsite...
                require('brace/mode/html')                
                require('brace/mode/javascript')    //language
                require('brace/mode/less')
                require('brace/theme/twilight')
                require('brace/snippets/javascript') //snippet
            },

            getContent(cid, info){
                this.content = "";
                if(cid == "getContainerLog"){
                    this.dialogVisible = true;
                }else{
                    this.inspectDialogVisible = true;
                }
                let agentId= "";
                if(this.isSingleMode){
                    agentId = this.selectedAgentId;
                }else{
                    agentId = this.selectedAgentsData[0].value
                }
                let data = {};
                data.name = info.name;
                this.logLoading = true;
                getDeviceStatus(agentId, dockerAction[cid], data).then((obj) => {
                    this.logLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(cid == "getContainerLog"){
                                this.content = res.content.value;
                            }else{
                                this.inspectContent = JSON.parse(res.content.value);
                            }
                        }
                    })   
                })
            },

            monitor(name, containerId){
                let agentId= "";
                if(this.isSingleMode){
                    agentId = this.selectedAgentId;
                }else{
                    agentId = this.selectedAgentsData[0].value
                }
                this.$router.push({path:"/main/control/dockerMonitor", query: {name, endpoint: agentId, containerId}});
            }, 
            
            //batch install app
            batchSetAppSensor(cid, appdata){
                if(dockerAction[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                _g.openGlobalLoading();
                let taskname = this.dockerStatusForm.taskname;
                this.dialogTasknameVisible = false;                  
                batchExecDeviceStatus(dockerAction[cid], appdata).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status == "CHANGED"){
                            //show overview
                            setLocal("container_taskname", taskname);
                            this.activeName = "containerStatusHistory";
                            this.$refs.containerHistory.intervalGetContainerStatusHistory(); 
                        }else{
                            _g.handleError(res);
                        }
                    })
                })                       
            },

            batchinstall(){
                if(this.selectedAgentsData.length === 0){
                    this.$swal("", this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')});
                    return;
                }
                this.$refs.dockerStatusForm.validate((valid) => {
                    if(valid) {
                        let containerInfo = {};
                        let data = [];
                        this.multipleSelection.forEach(element => {
                            let obj = {};
                            obj.name = element.name;
                            obj.containerId = element.id
                            data.push(obj);
                        });
                        containerInfo.taskname = this.dockerStatusForm.taskname;
                        containerInfo.content = JSON.stringify(data);
                        containerInfo.groupname = this.groupname;
                        this.batchSetAppSensor(this.cid, containerInfo);
                        
                    }
                })
            },
            

             //get updatedp status count
            getBatchComposeStatusCount(){
                let container_taskname = getLocal("container_taskname");
                if(!container_taskname){
                    console.error("getBatchComposeStatusCount:localStorage(container_taskname) is null")
                    return;
                }
                getAppStatusOverviewApi(container_taskname, batchContainerType).then((data) => {
                    handleResponse((data), (res) => {
                        if(res){
                            this.container_taskname = container_taskname;

                            this.installing = res.installing;
                            this.failed = res.fail;
                            this.success = res.success;
                        }
                    })
                })
            },

            handleScroll(){
                let contentEle = document.getElementById("homeContant");
                var scrollTop = contentEle.pageYOffset || contentEle.scrollTop;
                if (scrollTop > this.offsetTop) {
                    this.isFixed = true
                } else {
                    this.isFixed = false
                }
            }
            
        },

        created() {
            if(this.$route.query.name){
                this.productname = this.$route.query.name;
                this.version = this.$route.query.version;
                this.selectedAgentId = this.$route.query.endpoint;
                this.getSensorStatus();
            }
        },
        computed:{
            containerTableList(){
                return this.containerTableData.filter(
                    data => !this.keywords || data.name.toLowerCase().includes(this.keywords.toLowerCase())
                )
            },
            isShowSearchInput(){
                if(this.activeName === "container"){
                    return true;
                }else{
                    return false;
                }
            },
            isShowOverview(){
                if(this.total > 0 &&  this.activeName === "containerStatusHistory"){
                    return true;
                }else{
                    return false;
                }
            },
            total(){
                return  this.success+ this.installing+ this.failed;
            },

            stopbtndisabled(){
                let res = true;
                if(this.multipleSelection.length>0){
                    this.multipleSelection.forEach((el) => {
                        if(el.state == "running"){
                           res = false; 
                        }
                    })
                }
                return res;
            },
            startbtndisabled(){
                let res = true;
                if(this.multipleSelection.length>0){
                    this.multipleSelection.forEach((el) => {
                        if(el.state != "running"&& el.state != "paused"){
                           res = false; 
                        }
                    })
                }
                return res;

            },
            restartbtndisabled(){
                if(this.multipleSelection.length>0){
                    return false;
                }else{
                    return true;
                }
                
            },
            killbtndisabled(){
                let res = true;
                if(this.multipleSelection.length>0){
                    this.multipleSelection.forEach((el) => {
                        if(el.state == "running"|| el.state == "paused"){
                           res = false; 
                        }
                    })
                }
                return res;

            },
            pausebtndisabled(){
                let res = true;
                if(this.multipleSelection.length>0){
                    this.multipleSelection.forEach((el) => {
                        if(el.state == "running"){
                           res = false; 
                        }
                    })
                }
                return res;

            },
            resumebtndisabled(){
                let res = true;
                if(this.multipleSelection.length>0){
                    this.multipleSelection.forEach((el) => {
                        if(el.state == "paused"){
                           res = false; 
                        }
                    })
                }
                return res;

            },
            removebtndisabled(){
                if(this.multipleSelection.length>0){
                    return false;
                }else{
                    return true;
                }
            },

            btnGroupIsShow(){
                if(this.activeName == "container"){
                    return true;
                }else{
                    return false;
                }
            }
        },
        mounted() {
            let contentEle = document.getElementById("homeContant");
            this.offsetTop = document.getElementById("actionID").offsetTop;
            contentEle.addEventListener('scroll', this.handleScroll)
        },

        beforeDestroy() {
            let contentEle = document.getElementById("homeContant");
            contentEle.removeEventListener('scroll', this.handleScroll)
        },

    }
</script>





